'use client';

import { useState, useEffect } from 'react';
import { format } from 'date-fns';
import { useAuth } from '@/hooks/useAuth';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Input } from '@/components/ui/input';
import { Textarea } from '@/components/ui/textarea';
import { Dialog, DialogContent, DialogDescription, DialogFooter, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog';
import { toast } from 'sonner';

interface User {
  firstName: string;
  lastName: string;
  email: string;
  blockNumber: string;
  lotNumber: string;
}

interface Ticket {
  id: string;
  title: string;
  description: string;
  imageUrl?: string;
  status: 'pending' | 'in-progress' | 'resolved';
  createdAt: string;
  user: User | null;
}

interface Booking {
  id: string;
  type: 'punchlist' | 'unit-acceptance';
  preferredDate: string;
  status: 'pending' | 'approved' | 'rejected';
  adminNotes?: string;
  createdAt: string;
  user: User | null;
}

export default function AdminDashboardPage() {
  const { user, logout } = useAuth();
  const [tickets, setTickets] = useState<Ticket[]>([]);
  const [bookings, setBookings] = useState<Booking[]>([]);
  const [filteredTickets, setFilteredTickets] = useState<Ticket[]>([]);
  const [blockFilter, setBlockFilter] = useState<string>('');
  const [lotFilter, setLotFilter] = useState<string>('');
  const [statusFilter, setStatusFilter] = useState<string>('');
  const [selectedBooking, setSelectedBooking] = useState<Booking | null>(null);
  const [adminNotes, setAdminNotes] = useState('');
  const [isUpdating, setIsUpdating] = useState(false);
  const [ticketsLoading, setTicketsLoading] = useState(false);
  const [bookingsLoading, setBookingsLoading] = useState(false);

  useEffect(() => {
    if (user?.isAdmin) {
      fetchTickets();
      fetchBookings();
    }
  }, [user]);

  useEffect(() => {
    applyTicketFilters();
  }, [tickets, blockFilter, lotFilter, statusFilter]);

  const fetchTickets = async () => {
    setTicketsLoading(true);
    try {
      const token = localStorage.getItem('auth-token');
      if (!token) {
        console.error('No authentication token found');
        toast.error('Authentication required. Please log in again.');
        logout();
        return;
      }

      const response = await fetch('/api/admin/tickets', {
        headers: {
          'Authorization': `Bearer ${token}`,
        },
      });

      if (response.ok) {
        const data = await response.json();
        setTickets(data.tickets);
      } else if (response.status === 401) {
        console.error('Unauthorized access to tickets');
        toast.error('Session expired. Please log in again.');
        logout();
      } else if (response.status === 403) {
        console.error('Admin access required');
        toast.error('Access denied. Admin privileges required.');
      } else {
        const errorData = await response.json();
        console.error('Failed to fetch tickets:', errorData.message);
        toast.error(errorData.message || 'Failed to fetch tickets');
      }
    } catch (error) {
      console.error('Network error while fetching tickets:', error);
      toast.error('Network error. Please check your connection and try again.');
    } finally {
      setTicketsLoading(false);
    }
  };

  const fetchBookings = async () => {
    setBookingsLoading(true);
    try {
      const token = localStorage.getItem('auth-token');
      const response = await fetch('/api/admin/bookings', {
        headers: {
          'Authorization': `Bearer ${token}`,
        },
      });

      if (response.ok) {
        const data = await response.json();
        setBookings(data.bookings);
      } else if (response.status === 401) {
        console.error('Unauthorized access to bookings');
        toast.error('Session expired. Please log in again.');
        logout();
      } else if (response.status === 403) {
        console.error('Admin access required for bookings');
        toast.error('Access denied. Admin privileges required.');
      } else {
        const errorData = await response.json();
        console.error('Failed to fetch bookings:', errorData.message);
        toast.error(errorData.message || 'Failed to fetch bookings');
      }
    } catch (error) {
      console.error('Network error while fetching bookings:', error);
      toast.error('Network error. Please check your connection and try again.');
    } finally {
      setBookingsLoading(false);
    }
  };

  const applyTicketFilters = () => {
    let filtered = tickets;

    if (blockFilter) {
      filtered = filtered.filter(ticket => ticket.user?.blockNumber === blockFilter);
    }

    if (lotFilter) {
      filtered = filtered.filter(ticket => ticket.user?.lotNumber === lotFilter);
    }

    if (statusFilter) {
      filtered = filtered.filter(ticket => ticket.status === statusFilter);
    }

    setFilteredTickets(filtered);
  };

  const updateBookingStatus = async (bookingId: string, status: 'approved' | 'rejected') => {
    setIsUpdating(true);
    try {
      const token = localStorage.getItem('auth-token');
      const response = await fetch('/api/admin/bookings', {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`,
        },
        body: JSON.stringify({
          bookingId,
          status,
          adminNotes: adminNotes.trim() || undefined,
        }),
      });

      if (response.ok) {
        toast.success(`Booking ${status} successfully!`);
        setSelectedBooking(null);
        setAdminNotes('');
        fetchBookings();
      } else {
        const error = await response.json();
        toast.error(error.message || `Failed to ${status} booking`);
      }
    } catch (error) {
      toast.error(`Failed to ${status} booking`);
    } finally {
      setIsUpdating(false);
    }
  };

  const getStatusBadge = (status: string) => {
    const variants: Record<string, 'default' | 'secondary' | 'destructive'> = {
      pending: 'default',
      'in-progress': 'secondary',
      resolved: 'secondary',
      approved: 'secondary',
      rejected: 'destructive',
    };

    return (
      <Badge variant={variants[status] || 'default'}>
        {status.charAt(0).toUpperCase() + status.slice(1)}
      </Badge>
    );
  };

  const getUniqueBlocks = () => {
    const blocks = new Set(tickets.map(ticket => ticket.user?.blockNumber).filter(Boolean) as string[]);
    return Array.from(blocks).sort();
  };

  const getUniqueLots = () => {
    const lots = new Set(tickets.map(ticket => ticket.user?.lotNumber).filter(Boolean) as string[]);
    return Array.from(lots).sort();
  };

  if (!user?.isAdmin) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <Card className="w-full max-w-md">
          <CardHeader className="text-center">
            <CardTitle>Access Denied</CardTitle>
            <CardDescription>
              You need admin privileges to access this page.
            </CardDescription>
          </CardHeader>
          <CardContent className="text-center">
            <Button onClick={() => window.location.href = '/'}>
              Go to Home
            </Button>
          </CardContent>
        </Card>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-background">
      <header className="border-b bg-card">
        <div className="container mx-auto px-4 py-4 flex justify-between items-center">
          <div>
            <h1 className="text-2xl font-bold">Admin Dashboard</h1>
            <p className="text-muted-foreground">
              Property Management System Administration
            </p>
          </div>
          <div className="flex gap-2">
            <Button variant="outline" onClick={() => window.location.href = '/admin/users'}>
              View Users
            </Button>
            <Button variant="outline" onClick={logout}>
              Sign Out
            </Button>
          </div>
        </div>
      </header>

      <div className="container mx-auto px-4 py-8">
        <Tabs defaultValue="bookings" className="space-y-6">
          <TabsList className="grid w-full grid-cols-2">
            <TabsTrigger value="bookings">Booking Requests</TabsTrigger>
            <TabsTrigger value="tickets">Client Tickets</TabsTrigger>
          </TabsList>

          <TabsContent value="bookings">
            <Card>
              <CardHeader>
                <CardTitle>Booking Requests</CardTitle>
                <CardDescription>
                  Review and manage client booking requests
                </CardDescription>
              </CardHeader>
              <CardContent>
                {bookingsLoading ? (
                  <p className="text-center text-muted-foreground py-8">
                    Loading bookings...
                  </p>
                ) : bookings.length === 0 ? (
                  <p className="text-center text-muted-foreground py-8">
                    No booking requests yet.
                  </p>
                ) : (
                  <div className="space-y-4">
                    {bookings.map((booking) => (
                      <div key={booking.id} className="border rounded-lg p-4">
                        <div className="flex justify-between items-start mb-4">
                          <div>
                            <h3 className="font-semibold capitalize">
                              {booking.type.replace('-', ' ')} Appointment
                            </h3>
                            <p className="text-sm text-muted-foreground">
                              {booking.user ? (
                                <>
                                  {booking.user.firstName} {booking.user.lastName} - 
                                  Block {booking.user.blockNumber}, Lot {booking.user.lotNumber}
                                </>
                              ) : (
                                'Unknown Client'
                              )}
                            </p>
                            <p className="text-sm text-muted-foreground">
                              {booking.user?.email}
                            </p>
                          </div>
                          {getStatusBadge(booking.status)}
                        </div>

                        <div className="grid md:grid-cols-2 gap-4 mb-4">
                          <div>
                            <p className="text-sm font-medium">Preferred Date:</p>
                            <p className="text-sm text-muted-foreground">
                              {(() => {
                                try {
                                  return format(new Date(booking.preferredDate), 'PPP');
                                } catch (e) {
                                  return booking.preferredDate;
                                }
                              })()}
                            </p>
                          </div>
                          <div>
                            <p className="text-sm font-medium">Requested:</p>
                            <p className="text-sm text-muted-foreground">
                              {(() => {
                                try {
                                  return format(new Date(booking.createdAt), 'PPP');
                                } catch (e) {
                                  return booking.createdAt;
                                }
                              })()}
                            </p>
                          </div>
                        </div>

                        {booking.adminNotes && (
                          <div className="mb-4">
                            <p className="text-sm font-medium">Admin Notes:</p>
                            <p className="text-sm bg-muted p-2 rounded">
                              {booking.adminNotes}
                            </p>
                          </div>
                        )}

                        {booking.status === 'pending' && (
                          <div className="flex gap-2">
                            <Dialog>
                              <DialogTrigger asChild>
                                <Button 
                                  variant="default" 
                                  size="sm"
                                  onClick={() => {
                                    setSelectedBooking(booking);
                                    setAdminNotes('');
                                  }}
                                >
                                  Approve
                                </Button>
                              </DialogTrigger>
                              <DialogContent>
                                <DialogHeader>
                                  <DialogTitle>Approve Booking</DialogTitle>
                                  <DialogDescription>
                                    Approve this {booking.type.replace('-', ' ')} appointment for{' '}
                                    {(() => {
                                      try {
                                        return format(new Date(booking.preferredDate), 'PPP');
                                      } catch (e) {
                                        return booking.preferredDate;
                                      }
                                    })()}
                                  </DialogDescription>
                                </DialogHeader>
                                <div className="space-y-4">
                                  <div>
                                    <label className="text-sm font-medium">
                                      Confirmation Notes (Optional)
                                    </label>
                                    <Textarea
                                      placeholder="Add any additional notes for the client..."
                                      value={adminNotes}
                                      onChange={(e) => setAdminNotes(e.target.value)}
                                      className="mt-1"
                                    />
                                  </div>
                                </div>
                                <DialogFooter>
                                  <Button
                                    onClick={() => updateBookingStatus(booking.id, 'approved')}
                                    disabled={isUpdating}
                                  >
                                    {isUpdating ? 'Approving...' : 'Approve Booking'}
                                  </Button>
                                </DialogFooter>
                              </DialogContent>
                            </Dialog>

                            <Dialog>
                              <DialogTrigger asChild>
                                <Button 
                                  variant="destructive" 
                                  size="sm"
                                  onClick={() => {
                                    setSelectedBooking(booking);
                                    setAdminNotes('');
                                  }}
                                >
                                  Reject
                                </Button>
                              </DialogTrigger>
                              <DialogContent>
                                <DialogHeader>
                                  <DialogTitle>Reject Booking</DialogTitle>
                                  <DialogDescription>
                                    Reject this {booking.type.replace('-', ' ')} appointment and ask the client to reschedule
                                  </DialogDescription>
                                </DialogHeader>
                                <div className="space-y-4">
                                  <div>
                                    <label className="text-sm font-medium">
                                      Reason for Rejection
                                    </label>
                                    <Textarea
                                      placeholder="Please explain why this booking needs to be rescheduled..."
                                      value={adminNotes}
                                      onChange={(e) => setAdminNotes(e.target.value)}
                                      className="mt-1"
                                      required
                                    />
                                  </div>
                                </div>
                                <DialogFooter>
                                  <Button
                                    variant="destructive"
                                    onClick={() => updateBookingStatus(booking.id, 'rejected')}
                                    disabled={isUpdating || !adminNotes.trim()}
                                  >
                                    {isUpdating ? 'Rejecting...' : 'Reject Booking'}
                                  </Button>
                                </DialogFooter>
                              </DialogContent>
                            </Dialog>
                          </div>
                        )}
                      </div>
                    ))}
                  </div>
                )}
              </CardContent>
            </Card>
          </TabsContent>

          <TabsContent value="tickets">
            <Card>
              <CardHeader>
                <CardTitle>Client Tickets</CardTitle>
                <CardDescription>
                  View and filter client-submitted tickets
                </CardDescription>
              </CardHeader>
              <CardContent>
                {/* Filters */}
                <div className="grid md:grid-cols-4 gap-4 mb-6">
                  <div>
                    <label className="text-sm font-medium mb-1 block">Block Number</label>
                    <Select value={blockFilter} onValueChange={setBlockFilter}>
                      <SelectTrigger>
                        <SelectValue placeholder="All blocks" />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectItem value="">All blocks</SelectItem>
                        {getUniqueBlocks().map((block) => (
                          <SelectItem key={block} value={block}>
                            Block {block}
                          </SelectItem>
                        ))}
                      </SelectContent>
                    </Select>
                  </div>

                  <div>
                    <label className="text-sm font-medium mb-1 block">Lot Number</label>
                    <Select value={lotFilter} onValueChange={setLotFilter}>
                      <SelectTrigger>
                        <SelectValue placeholder="All lots" />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectItem value="">All lots</SelectItem>
                        {getUniqueLots().map((lot) => (
                          <SelectItem key={lot} value={lot}>
                            Lot {lot}
                          </SelectItem>
                        ))}
                      </SelectContent>
                    </Select>
                  </div>

                  <div>
                    <label className="text-sm font-medium mb-1 block">Status</label>
                    <Select value={statusFilter} onValueChange={setStatusFilter}>
                      <SelectTrigger>
                        <SelectValue placeholder="All statuses" />
                      </SelectTrigger>
                      <SelectContent>
                        <SelectItem value="">All statuses</SelectItem>
                        <SelectItem value="pending">Pending</SelectItem>
                        <SelectItem value="in-progress">In Progress</SelectItem>
                        <SelectItem value="resolved">Resolved</SelectItem>
                      </SelectContent>
                    </Select>
                  </div>

                  <div className="flex items-end">
                    <Button 
                      variant="outline" 
                      onClick={() => {
                        setBlockFilter('');
                        setLotFilter('');
                        setStatusFilter('');
                      }}
                      className="w-full"
                    >
                      Clear Filters
                    </Button>
                  </div>
                </div>

                {ticketsLoading ? (
                  <p className="text-center text-muted-foreground py-8">
                    Loading tickets...
                  </p>
                ) : filteredTickets.length === 0 ? (
                  <p className="text-center text-muted-foreground py-8">
                    {tickets.length === 0 ? 'No tickets submitted yet.' : 'No tickets match the current filters.'}
                  </p>
                ) : (
                  <div className="space-y-4">
                    {filteredTickets.map((ticket) => (
                      <div key={ticket.id} className="border rounded-lg p-4">
                        <div className="flex justify-between items-start mb-4">
                          <div>
                            <h3 className="font-semibold">{ticket.title}</h3>
                            <p className="text-sm text-muted-foreground">
                              {ticket.user ? (
                                <>
                                  {ticket.user.firstName} {ticket.user.lastName} - 
                                  Block {ticket.user.blockNumber}, Lot {ticket.user.lotNumber}
                                </>
                              ) : (
                                'Unknown Client'
                              )}
                            </p>
                            <p className="text-sm text-muted-foreground">
                              {ticket.user?.email}
                            </p>
                          </div>
                          {getStatusBadge(ticket.status)}
                        </div>

                        <p className="text-muted-foreground mb-4">{ticket.description}</p>

                        {ticket.imageUrl && (
                          <div className="mb-4">
                            <img
                              src={ticket.imageUrl}
                              alt="Ticket attachment"
                              className="w-48 h-36 object-cover rounded border"
                              onError={(e) => {
                                e.currentTarget.style.display = 'none';
                              }}
                            />
                          </div>
                        )}

                        <p className="text-sm text-muted-foreground">
                          {(() => {
                            try {
                              return `Submitted: ${format(new Date(ticket.createdAt), 'PPP')}`;
                            } catch (e) {
                              // Fallback if date parsing fails
                              return `Submitted: ${ticket.createdAt}`;
                            }
                          })()}
                        </p>
                      </div>
                    ))}
                  </div>
                )}
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>
      </div>
    </div>
  );
}
